import { ref } from 'vue';
import { defineStore } from 'pinia';
const useStore = defineStore('app', () => {
    const isDark = ref(false);
    const isLoading = ref(false);
    const actions = ref([]);
    return { isDark, isLoading, actions };
});
const useCollectionStore = defineStore('collection', () => {
    const traits = ref([]);
    const layers = ref({});
    const directory = ref(''); // where the layers were loaded from
    const name = ref('');
    const size = ref(0);
    const width = ref(0);
    const height = ref(0);
    function reset() {
        this.traits = [];
        this.layers = {};
        this.directory = '';
        this.name = '';
        this.size = 0;
        this.width = 0;
        this.height = 0;
    }
    return { reset, traits, layers, directory, name, size, width, height };
}, { persist: true });
export { useStore, useCollectionStore };
